@charset "UTF-8";

@font-face {
  font-family: "icomoon";
  src: url("../fonts/icon-fonts/icomoon.eot");
  src: url("../fonts/icon-fonts/icomoon.eot") format("embedded-opentype"), url("../fonts/icon-fonts/icomoon.ttf") format("truetype"), url("../fonts/icon-fonts/icomoon.woff") format("woff"), url("../fonts/icon-fonts/icomoon.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='icon-']::before,
[class*=' icon-']::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

