  }

  .info-header__row {
    justify-content: center;
  }

  .info-header__title {
    text-align: center;
  }

  .datepicker-header__label {
    text-align: center;
  }

  .datepicker-header__body {
    padding: 0 10px;
  }

  .datepicker-header__item {
    width: 100%;
  }

  .card-gallery__image_row {
    flex: 0 0 40%;
  }

  .info-gallery__column_grid .info-gallery__label {
    flex: 1 1 auto;
  }

  .info-gallery__label {
    flex: 0 0 100%;
    line-height: 1.5;
  }
}

@media (min-width: 320px) and (max-width: 1920px) {
  .info-header__text {
    font-size: calc(12px + 2 * ((100vw - 320px) / 1600));
  }

  .title {
    font-size: calc(22px + 2 * ((100vw - 320px) / 1600));
  }

  .datepicker-header__label {
    font-size: calc(15px + 1 * ((100vw - 320px) / 1600));
  }

  .datepicker-header__input {
    font-size: calc(11px + 2 * ((100vw - 320px) / 1600));
  }

  .info-gallery__label {
    font-size: calc(14px + 2 * ((100vw - 320px) / 1600));
  }

  .info-gallery__link {
    font-size: calc(13px + 1 * ((100vw - 320px) / 1600));
  }

  .info-gallery__text {
    font-size: calc(12px + 2 * ((100vw - 320px) / 1600));
  }
}

@media (hover) {
  .gallery__btn:hover {
    color: #3d8eda;
    border: 1px solid #3d8eda;
  }
}